import { customColorAreaTagName } from "../constants";
import { CustomEventsHelper } from "../../blazor/custom-events-helper";
export class OnColorChangedEvent extends CustomEvent {
    constructor(red, green, blue) {
        super(OnColorChangedEvent.eventName, {
            detail: new OnColorChangedContext(red, green, blue),
            bubbles: true,
            composed: true,
            cancelable: true
        });
    }
}
OnColorChangedEvent.eventName = customColorAreaTagName + ".oncolorchanged";
export class OnColorChangedContext {
    constructor(red, green, blue) {
        this.Red = red;
        this.Green = green;
        this.Blue = blue;
    }
}
CustomEventsHelper.register(OnColorChangedEvent.eventName, x => {
    return x.detail;
});
